.logos {
	padding: 50px 0;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	transform-style: preserve-3d;
	overflow: hidden;
	z-index: 1;
	position: relative;
	&__logo {} }
.logo {
	width: 150px;
	height: 150px;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 10px;
	transform: translateZ(0);
	transform-style: preserve-3d;
	backface-visibility: hidden;
	transition: transform .3s ease;
	transform-origin: center;
	position: relative;
	&:before {
		position: absolute;
		content: '';
		display: block;
		width: 200%;
		height: 200%;
		left: -50%;
		top: -50%; }
	&:hover {
		//box-shadow: 2px 2px 30px 2px rgb(232, 41, 85), -2px -2px 30px 2px rgb(251, 146, 24)
		box-shadow: 10px 0 40px 0px rgba(251, 146, 24, .1), -10px 0 40px 0px rgba(232, 41, 85, .1), 0 10px 40px 0px rgba(251, 146, 24, .1), 0 -10px 40px 0px rgba(232, 41, 85, .1);
		//border: 1px solid red
		img {
			transform: translateZ(20px); } }
	&__content {
		transform-style: preserve-3d;
		pointer-events: none; }
	img {
		max-width: 100%;
		transition: transform .3s ease;
		pointer-events: none; } }



.preloader {
	width: 100%;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #000000;
	&.is-sequence {
		background-color: transparent; }
	//.preloader__content
 // &:before
 //   display: none
	&__inner {
		position: relative;
		opacity: 0;
		z-index: 2; }
	&__content {
		font-size: 22px;
		text-transform: uppercase;
		font-weight: 900;
		font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
		letter-spacing: 3px;
		color: #000;
		padding: 10px 10px 10px 5px;
		position: relative;
		min-height: 50px;
		display: flex;
		justify-content: center;
		align-items: center;
		&-bg {
			position: absolute;
			top: 0;
			left: 0;
			width: 0;
			height: 100%;
			background-color: #fff; }
		&:before {
			position: absolute;
			content: '';
			display: block;
			top: 0;
			left: -20px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 0 20px 55px 20px;
			border-color: transparent transparent #000 transparent;
			z-index: 1; } }
	&__text {
		z-index: 1; }
	.square {
		width: 50px;
		height: 50px;
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateY(-300px);
		z-index: 2;
		//transition: transform .3s ease
		&_blue {
			background: #ff3236 /* Old browsers */;
			background: -moz-linear-gradient(left, #17DCFC 0%, #2D72E3 100%) /* FF3.6-15 */;
			background: -webkit-linear-gradient(left, #17DCFC 0%,#2D72E3 100%) /* Chrome10-25,Safari5.1-6 */;
			background: linear-gradient(to right, #17DCFC 0%,#2D72E3 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#17DCFC', endColorstr='#2D72E3',GradientType=1 ); }
		&_purple {
			background: #2eea2e /* Old browsers */;
			background: -moz-linear-gradient(left, #5A25D6 0%, #3562E2 100%) /* FF3.6-15 */;
			background: -webkit-linear-gradient(left, #5A25D6 0%,#3562E2 100%) /* Chrome10-25,Safari5.1-6 */;
			background: linear-gradient(to right, #5A25D6 0%,#3562E2 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5A25D6', endColorstr='#3562E2',GradientType=1 ); }
		&_yellow {
			background: #E1930B /* Old browsers */;
			background: -moz-linear-gradient(left, #E20B66 0%, #E1930B 100%) /* FF3.6-15 */;
			background: -webkit-linear-gradient(left, #E20B66 0%,#E1930B 100%) /* Chrome10-25,Safari5.1-6 */;
			background: linear-gradient(to right, #E20B66 0%, #E1930B 100%) /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */;
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#E20B66', endColorstr='#E1930B',GradientType=1 ); }
		&_shadow {
			opacity: 0.2; }
		&_md {
			width: 40px;
			height: 40px;
			margin-top: 5px;
			margin-left: 5px; }
		&_sm {
			width: 20px;
			height: 20px;
			margin-top: 15px;
			margin-left: 15px; } } }
.sequence {
	position: absolute;
	top: 0;
	left: 0;
	&.is-finished {
		opacity: 0;
		pointer-events: none; } }
