.canvas {
  position: relative;
  width: 800px;
  height: 600px;
  border: 1px solid black;
}

.canvas .p {
  position: absolute;
  width: 800px;
  height: 600px;
  left: 0;
  top: 0;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.triangles {
  border: 1px solid blueviolet;
  width: 800px;
  height: 600px;
}

.ballz-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.ballz {
  border: none;
  background: #000;
  width: 800px;
  height: 600px;
  position: relative;
}

.paint {
  display: flex;
}

.paint__title {
  font-size: 20px;
  text-align: center;
  line-height: 1.2;
  color: #FFFFFF;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 20px;
}

.paint__controls {
  width: 100px;
  background-color: blueviolet;
  padding-top: 10px;
}

.paint canvas {
  display: block;
}

.paint__actions {
  display: flex;
  flex-direction: column;
  padding: 0 5px;
}

.paint__actions .btn:not(:last-child) {
  margin-bottom: 10px;
}

.paint .rangeslider:not(:last-child) {
  margin-bottom: 10px;
}

.btn {
  border: 1px solid #ffffff;
  text-decoration: none;
  background-color: transparent;
  color: #FFFFFF;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 10px;
  position: relative;
  overflow: hidden;
}

.btn:before {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background-color: #FFFFFF;
  top: 50%;
  left: 50%;
  opacity: 0;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: transform .2s ease, opacity .2s ease;
}

.btn:hover:before {
  opacity: .4;
  transform: translate(-50%, -50%) scale(1.5, 1) rotate(90deg);
}

.btn_space {
  width: 100%;
}

.input-block__label {
  margin-bottom: 5px;
}

.input-block input {
  max-width: 100%;
}

.squares {
  line-height: 0;
}

.cursor {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  z-index: 5;
}

.waves {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.whiteweb {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.whiteweb img {
  max-width: 100%;
}

.wrapper {
  width: 100%;
  padding: 10px;
}

.some-block {
  padding: 20px;
  background-color: grey;
}

.portfolio {
  width: 100%;
}

.preloader {
  width: 100%;
}

.logos {
  padding: 50px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  transform-style: preserve-3d;
  overflow: hidden;
  z-index: 1;
  position: relative;
}

.logo {
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transform: translateZ(0);
  transform-style: preserve-3d;
  backface-visibility: hidden;
  transition: transform .3s ease;
  transform-origin: center;
  position: relative;
}

.logo:before {
  position: absolute;
  content: '';
  display: block;
  width: 200%;
  height: 200%;
  left: -50%;
  top: -50%;
}

.logo:hover {
  box-shadow: 10px 0 40px 0px rgba(251, 146, 24, 0.1), -10px 0 40px 0px rgba(232, 41, 85, 0.1), 0 10px 40px 0px rgba(251, 146, 24, 0.1), 0 -10px 40px 0px rgba(232, 41, 85, 0.1);
}

.logo:hover img {
  transform: translateZ(20px);
}

.logo__content {
  transform-style: preserve-3d;
  pointer-events: none;
}

.logo img {
  max-width: 100%;
  transition: transform .3s ease;
  pointer-events: none;
}

.preloader {
  width: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #000000;
}

.preloader.is-sequence {
  background-color: transparent;
}

.preloader__inner {
  position: relative;
  opacity: 0;
  z-index: 2;
}

.preloader__content {
  font-size: 22px;
  text-transform: uppercase;
  font-weight: 900;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  letter-spacing: 3px;
  color: #000;
  padding: 10px 10px 10px 5px;
  position: relative;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preloader__content-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: #fff;
}

.preloader__content:before {
  position: absolute;
  content: '';
  display: block;
  top: 0;
  left: -20px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 20px 55px 20px;
  border-color: transparent transparent #000 transparent;
  z-index: 1;
}

.preloader__text {
  z-index: 1;
}

.preloader .square {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateY(-300px);
  z-index: 2;
}

.preloader .square_blue {
  background: #ff3236;
  background: -moz-linear-gradient(left, #17DCFC 0%, #2D72E3 100%);
  background: -webkit-linear-gradient(left, #17DCFC 0%, #2D72E3 100%);
  background: linear-gradient(to right, #17DCFC 0%, #2D72E3 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#17DCFC', endColorstr='#2D72E3',GradientType=1 );
}

.preloader .square_purple {
  background: #2eea2e;
  background: -moz-linear-gradient(left, #5A25D6 0%, #3562E2 100%);
  background: -webkit-linear-gradient(left, #5A25D6 0%, #3562E2 100%);
  background: linear-gradient(to right, #5A25D6 0%, #3562E2 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5A25D6', endColorstr='#3562E2',GradientType=1 );
}

.preloader .square_yellow {
  background: #E1930B;
  background: -moz-linear-gradient(left, #E20B66 0%, #E1930B 100%);
  background: -webkit-linear-gradient(left, #E20B66 0%, #E1930B 100%);
  background: linear-gradient(to right, #E20B66 0%, #E1930B 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#E20B66', endColorstr='#E1930B',GradientType=1 );
}

.preloader .square_shadow {
  opacity: 0.2;
}

.preloader .square_md {
  width: 40px;
  height: 40px;
  margin-top: 5px;
  margin-left: 5px;
}

.preloader .square_sm {
  width: 20px;
  height: 20px;
  margin-top: 15px;
  margin-left: 15px;
}

.sequence {
  position: absolute;
  top: 0;
  left: 0;
}

.sequence.is-finished {
  opacity: 0;
  pointer-events: none;
}

.swiper_vertical {
  height: 500px;
}

.swiper_vertical .swiper-slide {
  min-height: auto;
}

.swiper-container {
  color: #000;
  font-size: 50px;
}

.swiper-container:not(:last-child) {
  margin-bottom: 50px;
}

.swiper-slide {
  min-height: 500px;
  border: 1px solid slateblue;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.swiper-button-prev, .swiper-button-next {
  cursor: none;
}

.map {
  height: 500px;
}

.smile > g {
  opacity: 0;
}

.smile > g.is-active {
  opacity: 1;
}

.bg {
  height: 100vh;
  min-width: 199vh;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0;
  display: flex;
  flex-direction: column;
}

.bg > .smile {
  display: none;
}

.bg__row {
  display: flex;
}

.bg-cont {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.bg svg {
  display: inline-block;
}

body {
  margin: 0;
}

.bg__smile {
  width: 8.33333%;
  height: 16.66667vh;
}

.smile {
  width: 100%;
  height: 100%;
}
